<template>
    <div class="wp_login_form">
        <div class="center">
            <van-field
                    v-model="phone"
                    left-icon="phone"
                    placeholder=""
                    :error-message="errMessage"
                    right-icon="question-o"
                    clearable
                    @click-right-icon="tip"></van-field>

            <van-field
                    v-model="smsCode"
                    left-icon="lock"
                    center
                    clearable
                    placeholder=""
            >
                <slot name="send">
                    <van-button slot="button" size="small" type="primary"></van-button>
                </slot>
            </van-field>
            <van-button type="primary" style="width:100%" :disabled="disabled" @click="submit">
            </van-button>
        </div>
        <div class="bottom">
            <slot name="bottom"></slot>
        </div>
    </div>
</template>

<script>

    export default {
        name: "wp_login_form",
        components: {},
        data() {
            return {
                msg: '',
                errMessage: "",
                disabled: true,
            }
        },
        props: {
            phone: String,
            smsCode: String,
            tipMsg:{
                type:String,
                default:""
            }
        },
        watch: {
            phone: function () {
                var myreg = /^(((13[0-9]{1})|(15[0-9]{1})|(17[0-9]{1})|(18[0-9]{1}))+\d{8})$/;
                if (this.phone.length === 11) {
                    if (!myreg.test(this.phone)) {
                        this.errMessage = "";
                    } else {
                        this.errMessage = "";
                        this.disabled = false;
                    }
                } else if (this.phone.length > 11) {
                    this.errMessage = "";
                    this.disabled = true;
                } else {
                    this.disabled = true;
                }
            },
            smsCode: function (){

            }
        },
        methods: {
            tip(){
                if(this.tipMsg) {
                    this.$toast(this.tipMsg)
                }
            },
            sendSms() {
                this.$toast("sendSms")
            },
            submit() {
                var imei = '123456'
                var url = 'index.html?phone=' + this.phone + '&imei=' + imei
                this.$http.get(url).then((res) => {
                    this.$toast(',');
                    this.active = 1;
                    // console.log(res);
                })
            }
        }
    }
</script>

